import React from 'react'
import './Login.css'
import Logo from '../../assets/logo.png'


const Login = () => {
  return (
    <form className='login'>
        <img src={Logo} alt="" />
        <input type="text" id='id' placeholder='  '/>
       
        <input id='password' type="password" placeholder='' />
        <button></button>
    </form>
  )
}

export default Login